/***************************************************
 * > Typography
***************************************************/
@font-face {
  font-family: "TitilliumWeb";
  src: local("TitilliumWeb"), local("TitilliumWeb Regular"), local("titillium"), url("../fonts/TitilliumWeb-Regular.woff2") format("woff2"), url("../fonts/TitilliumWeb-Regular.woff") format("woff"), url("../fonts/TitilliumWeb-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TitilliumWeb";
  src: local("TitilliumWeb"), local("TitilliumWeb Bold"), local("titillium"), url("../fonts/TitilliumWeb-Bold.woff2") format("woff2"), url("../fonts/TitilliumWeb-Bold.woff") format("woff"), url("../fonts/TitilliumWeb-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
/***************************************************
 * > Normalize CSS
***************************************************/
html {
  font-family: sans-serif;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Remove default margin.
 */
body {
  margin: 0;
}

/* HTML5 display definitions
   ========================================================================== */
/**
 * Correct `block` display not defined for any HTML5 element in IE 8/9.
 * Correct `block` display not defined for `details` or `summary` in IE 10/11
 * and Firefox.
 * Correct `block` display not defined for `main` in IE 11.
 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

/**
 * 1. Correct `inline-block` display not defined in IE 8/9.
 * 2. Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.
 */
audio,
canvas,
progress,
video {
  display: inline-block;
  /* 1 */
  vertical-align: baseline;
  /* 2 */
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address `[hidden]` styling not present in IE 8/9/10.
 * Hide the `template` element in IE 8/9/10/11, Safari, and Firefox < 22.
 */
[hidden],
template {
  display: none;
}

/* Links
   ========================================================================== */
/**
 * Remove the gray background color from active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * Improve readability of focused elements when they are also in an
 * active/hover state.
 */
a:active,
a:hover {
  outline: 0;
}

/* Text-level semantics
   ========================================================================== */
/**
 * Address styling not present in IE 8/9/10/11, Safari, and Chrome.
 */
abbr[title] {
  border-bottom: 1px dotted;
}

/**
 * Address style set to `bolder` in Firefox 4+, Safari, and Chrome.
 */
b,
strong {
  font-weight: bold;
}

/**
 * Address styling not present in Safari and Chrome.
 */
dfn {
  font-style: italic;
}

/**
 * Address variable `h1` font-size and margin within `section` and `article`
 * contexts in Firefox 4+, Safari, and Chrome.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/**
 * Address styling not present in IE 8/9.
 */
mark {
  background: #ff0;
  color: #000;
}

/**
 * Address inconsistent and variable font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` affecting `line-height` in all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove border when inside `a` element in IE 8/9/10.
 */
img {
  border: 0;
}

/**
 * Correct overflow not hidden in IE 9/10/11.
 */
svg:not(:root) {
  overflow: hidden;
}

/* Grouping content
   ========================================================================== */
/**
 * Address margin not present in IE 8/9 and Safari.
 */
figure {
  margin: 1em 40px;
}

/**
 * Address differences between Firefox and other browsers.
 */
hr {
  box-sizing: content-box;
  height: 0;
}

/**
 * Contain overflow in all browsers.
 */
pre {
  overflow: auto;
}

/**
 * Address odd `em`-unit font size rendering in all browsers.
 */
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

/* Forms
   ========================================================================== */
/**
 * Known limitation: by default, Chrome and Safari on OS X allow very limited
 * styling of `select`, unless a `border` property is set.
 */
/**
 * 1. Correct color not being inherited.
 *    Known issue: affects color of disabled elements.
 * 2. Correct font properties not being inherited.
 * 3. Address margins set differently in Firefox 4+, Safari, and Chrome.
 */
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  /* 1 */
  font: inherit;
  /* 2 */
  margin: 0;
  /* 3 */
}

/**
 * Address `overflow` set to `hidden` in IE 8/9/10/11.
 */
button {
  overflow: visible;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Firefox, IE 8/9/10/11, and Opera.
 * Correct `select` style inheritance in Firefox.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. CUSTOM FOR WEBFLOW: Removed the input[type="submit"] selector to reduce
 *    specificity and defer to the .w-button selector
 */
button,
html input[type=button],
input[type=reset] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * Remove inner padding and border in Firefox 4+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * Address Firefox 4+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
input {
  line-height: normal;
}

/**
 * It's recommended that you don't attempt to style these elements.
 * Firefox's implementation doesn't respect box-sizing, padding, or width.
 *
 * 1. Address box sizing set to `content-box` in IE 8/9/10.
 * 2. Remove excess padding in IE 8/9/10.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

/**
 * Fix the cursor style for Chrome's increment/decrement buttons. For certain
 * `font-size` values of the `input`, it causes the cursor style of the
 * decrement button to change from `default` to `text`.
 */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. CUSTOM FOR WEBFLOW: changed from `textfield` to `none` to normalize iOS rounded input
 * 2. CUSTOM FOR WEBFLOW: box-sizing: content-box rule removed
 *    (similar to normalize.css >=4.0.0)
 */
input[type=search] {
  -webkit-appearance: none;
  /* 1 */
}

/**
 * Remove inner padding and search cancel button in Safari and Chrome on OS X.
 * Safari (but not Chrome) clips the cancel button when the search input has
 * padding (and `textfield` appearance).
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct `color` not being inherited in IE 8/9/10/11.
 * 2. Remove padding so people aren't caught out if they zero out fieldsets.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */
}

/**
 * Remove default vertical scrollbar in IE 8/9/10/11.
 */
textarea {
  overflow: auto;
}

/**
 * Don't inherit the `font-weight` (applied by a rule above).
 * NOTE: the default cannot safely be changed in Chrome and Safari on OS X.
 */
optgroup {
  font-weight: bold;
}

/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/******************************************************************
* > Accessibility 
******************************************************************/
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  font-size: 1em;
  font-weight: bold;
  padding: 15px 23px 14px;
  color: #333;
  background: #fff;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
}

/* Display outline on focus */
:focus {
  color: #333;
  outline: #ccc solid 1px;
}

/* ## Accessible Menu
--------------------------------------------- */
.menu .menu-item:focus {
  position: static;
}

.menu .menu-item > a:focus + ul.sub-menu,
.menu .menu-item.sf-hover > ul.sub-menu {
  left: auto;
  opacity: 1;
}

/***************************************************
 * > Globals
***************************************************/
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  background-color: #1E3358;
  color: #fff;
  font-family: TitilliumWeb, sans-serif;
  font-size: 1vw;
  font-style: normal;
  font-weight: 400;
  line-height: 2.95rem;
  letter-spacing: 0;
  min-height: 100vh;
}

.site-container {
  position: relative;
}

p,
li {
  font-size: 14px;
}

ul,
ol {
  list-style-type: none;
}

a {
  color: #fff;
  text-decoration: none;
}
a:hover {
  color: #F15A29;
}

#workshops p {
  color: #F5F5F5;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-weight: 700;
  text-transform: uppercase;
}

h1,
.h1 {
  font-size: calc(3rem + 2.5vw);
  letter-spacing: 0.75rem;
  line-height: calc(1.1em + 1.25vw);
  margin-bottom: 0;
}

h2,
.h2 {
  font-size: 2.4rem;
  font-weight: 700;
  letter-spacing: 0.25rem;
  line-height: 1.23;
}

h3,
.h3 {
  font-size: 1.8rem;
  letter-spacing: 0.15rem;
  line-height: 29.5px;
}

@media only screen and (min-width: 1200px) {
  h1,
.h1 {
    font-size: 10rem;
  }

  h3,
.h3 {
    margin-bottom: 2.5rem;
  }
}
/***************************************************
 * > Header
***************************************************/
.navbar {
  padding-top: 0;
  z-index: 1000;
}

.navbar-brand {
  padding-top: 2rem;
}

.navbar-collapse {
  position: absolute;
  top: 11rem;
  left: 0;
  right: 0;
  background-color: #F15A29;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.navbar-dark .navbar-toggler {
  border: 1px solid #fff;
}

.navbar-dark .navbar-nav .nav-link,
.nav-link.button {
  text-transform: uppercase;
  color: #fff;
  font-size: 1.8rem;
  line-height: 2.16rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #fff;
}

.subnav {
  padding-left: 0;
}

.nav-link.button {
  padding-left: 0;
  border-bottom: none;
}

@media only screen and (min-width: 992px) {
  .navbar-collapse {
    background: transparent;
    position: relative;
    top: 0;
    align-items: start;
    padding-top: 0;
  }

  .header--wrap.container {
    align-items: baseline;
  }

  .navbar-dark .navbar-nav .nav-link {
    border-bottom: none;
  }

  li.dropdown {
    margin-right: 10rem;
  }

  .navbar-dark .navbar-nav .dropdown .nav-link {
    padding-bottom: 0;
  }
  .navbar-dark .navbar-nav .subnav .sub--link.nav-link {
    padding-top: 0;
  }

  /* Register button */
  .nav-link.button {
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
    background: linear-gradient(to right, #4D2E90, #3EC2D5);
    padding-left: 6rem;
    padding-right: 6rem;
    border-radius: 50px;
  }
}
/***************************************************
 * > Content
***************************************************/
/** Hero **/
#hero {
  background-image: linear-gradient(rgba(30, 51, 88, 0.75), rgba(30, 51, 88, 0.75)), url(../images/btr-skyline-bw.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -10rem;
  height: 40rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px solid #fff;
}

.hero--subhead {
  color: #F05A28;
  font-weight: 700;
  font-size: 2rem;
  letter-spacing: 1px;
  line-height: 1.2;
  text-transform: uppercase;
}

@media only screen and (min-width: 768px) {
  #hero {
    margin-top: -13rem;
    height: 55rem;
  }
  #hero h1 {
    margin-bottom: 1.5rem;
  }
}
@media only screen and (min-width: 1200px) {
  #hero {
    margin-top: -13rem;
    height: 69.5rem;
  }
}
/** Workshops **/
#workshops {
  border-bottom: 1px solid #fff;
}

.w--wrap.container {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.w-filter {
  background: #F15A29;
  margin-top: 3rem;
  margin-bottom: 3rem;
  padding: 2rem;
  font-size: 1.8rem;
}
.w-filter .form-check-inline {
  display: flex;
  align-items: center;
}
.w-filter label {
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.w-filter input[type=checkbox] {
  appearance: none;
  border: none;
  background: #fff;
  width: 18px;
  height: 18px;
  margin-right: 20px;
  border-radius: 4px;
}
.w-filter input[type=checkbox]:checked {
  position: relative;
}
.w-filter input[type=checkbox]:checked:before {
  content: "✓";
  display: block;
  text-align: center;
  color: #1E3358;
  position: absolute;
  left: 0.4rem;
  top: 0;
  font-weight: 700;
}
.w-filter .w--icon svg {
  width: 18px;
  margin-right: 12px;
}

#w-results {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 0;
  padding-right: 0;
}

.workshop {
  position: relative;
  flex: 1 1 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #4D2E90, #CE2A7F, #F05A28);
  border-radius: 12px;
  cursor: pointer;
  height: 135px;
  margin-bottom: 2rem;
  transition: background-color 0.75s;
}
.workshop .w--cats {
  position: absolute;
  bottom: 10px;
  right: 15px;
  display: flex;
  justify-content: flex-end;
}
.workshop .w--icon {
  padding-left: 12px;
}
.workshop:hover {
  background: #F15A29;
}
.workshop .w--link:hover {
  color: #fff;
}

@media only screen and (min-width: 767px) {
  .w--wrap.container {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }

  .w-filter {
    display: flex;
    padding-right: 5.5rem;
    padding-left: 5.5rem;
    justify-content: space-between;
  }

  #w-results {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (min-width: 992px) {
  .w--wrap.container {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }

  .w-filter {
    margin-top: 4.4rem;
    margin-bottom: 4.4rem;
    padding-right: 7.5rem;
    padding-left: 7.5rem;
  }

  #w-results {
    grid-gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }

  .workshop {
    height: 226px;
  }
}
@media only screen and (min-width: 1200px) {
  #w-results {
    display: grid;
    grid-gap: 4rem;
    grid-template-columns: repeat(4, minmax(240px, 280px));
  }
}
/** Info **/
#info {
  border-bottom: 1px solid #fff;
}

#events,
#news {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

#info h2 {
  margin-bottom: 1.7rem;
}

#events {
  border-bottom: 1px solid #fff;
}

.event,
.news {
  display: block;
  padding: 2.3rem 0;
  border-bottom: 1px solid #78859B;
}
.event:last-of-type,
.news:last-of-type {
  border-bottom: none;
}

.event--link {
  display: flex;
  align-items: center;
}

.event--date {
  width: 7rem;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#4D2E90, #3EC2D5);
  border-radius: 12px;
  margin: 0 3rem 0 0;
}
.event--date .month {
  color: #13223C;
  font-size: 1.6rem;
  text-transform: uppercase;
}
.event--date .day {
  color: #13223C;
  font-size: 3.6rem;
  font-weight: 700;
  letter-spacing: 0.75px;
  line-height: 3rem;
}

.event--title,
.news--title {
  text-transform: none;
}

#info .meta {
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.95rem;
  margin-bottom: 0;
}

.news--more {
  font-size: 1.8rem;
  font-weight: 700;
  text-align: right;
  display: block;
}

@media only screen and (min-width: 768px) {
  #events,
#news {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}
@media only screen and (min-width: 992px) {
  .info--wrap {
    display: flex;
  }

  #events,
#news {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
    border-bottom: none;
    width: 50%;
  }

  #events {
    border-right: 1px solid #fff;
    padding-right: 5%;
  }

  #news {
    padding-left: 5%;
  }
}
/***************************************************
 * > Footer
***************************************************/
.footer--wrap {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.footer-logo {
  margin: 0 0 5rem;
}

.social-icons {
  width: 7rem;
  display: flex;
  justify-content: space-between;
}

@media only screen and (min-width: 768px) {
  .footer--wrap {
    padding-top: 6rem;
    padding-bottom: 6rem;
    display: flex;
    justify-content: space-between;
  }

  .footer-area.last {
    text-align: right;
  }

  .social-icons {
    width: auto;
    justify-content: flex-end;
  }

  .social-icon {
    padding-left: 2rem;
    height: 2.8rem;
  }
}
@media only screen and (min-width: 992px) {
  .footer--wrap {
    padding-top: 7.5rem;
    padding-bottom: 7.5rem;
  }
}